:root {
  --color: white;
}
html {
  background-color: var(--color);
  color: var(--color);
  font-family: Montserrat;
  transition: all 1s ease;
}
.actions-left {
  display:flex;
  flex-direction: column;
}
.actions-left button{
  width: 150px;
  transition: all 1s ease;
}
.actions-right button {
  margin-top:23px;
}
.actions-right button:first-of-type {
  margin-right: 7px;
}
#translate {
  margin-top:7px;
}
#quote-box {
  background-color: white;
  padding: 50px 35px 25px;
  width: 30%;
  margin: 20vh auto 0vh;
  min-width: 300px;
  border-bottom-left-radius: 35px;
  border-top-right-radius: 35px;
}
#text {
  font-style: italic;
  text-align: center;
}
#author {
  margin-top: 50px;
  text-align: right;
}
.actions {
  margin-top: 40px;
  display: flex;
  justify-content: space-between;
}
button {
  font-weight: 600;
  color: white;
  background-color: var(--color);
  border-radius: 5px;
  padding: 5px;
  font-size: 0.8em;
  border: 0;
  transition: all 1s ease;
  animation: fadeIn .5s ease-in;
}
button:hover {
  cursor:pointer;
}
i {
  font-size: 2em !important;
}
.fa-quote-left {
  margin-right:20px;
}
.fa-twitter, .fa-instagram {
  color: white;
  padding: 0px 2px;
}
#credits {
  color: white;
  text-align: center;
  margin: 0 auto;
  width: 120px;
  text-align:left;
}
p a {
  color: white;
  text-decoration: none;
  font-weight: 600;
}
@keyframes fadeIn {
  0% {opacity: 0;}
  100% {opacity: 1;}
} 
@media only screen and (max-width: 550px) {
  #quote-box {
    width:300px;
  }
}

